
import { defineComponent, PropType } from 'vue'

export default defineComponent({
  name: 'Hamburger',
  emits: ['toggleClick'],
  props: {
    isActive: {
      type: Boolean as PropType<boolean>,
      default: false
    }
  },
  setup(props, ctx) {
    const toggleClick = () => {
      ctx.emit('toggleClick')
    }
    return {
      toggleClick
    }
  }
})
